import React from "react";
import {
  Container,
  Grid,
  Box,
  Collapse,
  Alert,
  Typography,
  IconButton,
} from "@mui/material";
import huge from "../../assets/images/png/huge.png";
import CloseIcon from "@mui/icons-material/Close";
import {
  Image,
  ImageBackdrop,
  ImageSrc,
  ImageButton,
  ImageMarked,
} from "../../components";
import axiosInstance from "../../api/axiosInstance";
// import Cookies from "js-cookie";
import { useCookies } from 'react-cookie';

export default function Login() {
  const [open, setOpen] = React.useState(false);
  const [cookies, setCookie] = useCookies(['access_token']);

  const googleAuth = () => {
    window.open(
      `https://capable-semifreddo-7ce173.netlify.app/.netlify/functions/main/googleAuth/auth/google/callback`,
      "_self"
    );
  };

  React.useEffect(() => {
    axiosInstance
      .get("googleAuth/tokenSession", { withCredentials: true })
      .then((res) =>  setCookie('access_token', res.data.access_token, { maxAge: parseInt(res.data.maxAge) })/* Cookies.set("access_token", res.data.access_token, { expires: res.data.maxAge }) */);
  }, []);

  return (
    <Container maxWidth="lg">
      <Grid
        container
        spacing={0}
        direction="column"
        alignItems="center"
        justifyContent="center"
        style={{ minHeight: "100vh" }}
      >
        <Box
          sx={{
            display: "flex",
            flexWrap: "wrap",
            minWidth: 300,
            width: "100%",
            justifyContent: "center",
          }}
        >
          <ImageButton
            focusRipple
            key={"Google"}
            style={{
              width: "30%",
            }}
            onClick={() => googleAuth()}
          >
            <ImageSrc style={{ backgroundImage: `url(${huge})` }} />
            <ImageBackdrop className="MuiImageBackdrop-root" />
            <Image>
              <Typography
                component="span"
                variant="subtitle1"
                color="inherit"
                sx={{
                  position: "relative",
                  p: 4,
                  pt: 2,
                  pb: (theme) => `calc(${theme.spacing(1)} + 6px)`,
                }}
              >
                {"Sign in with Google "}
                <ImageMarked className="MuiImageMarked-root" />
              </Typography>
            </Image>
          </ImageButton>
        </Box>
        <Collapse in={open}>
          <Alert
            severity="error"
            action={
              <IconButton
                aria-label="close"
                color="inherit"
                size="small"
                onClick={() => {
                  setOpen(false);
                }}
              >
                <CloseIcon fontSize="inherit" />
              </IconButton>
            }
            sx={{ mb: 2 }}
          >
            erreur authentification
          </Alert>
        </Collapse>
      </Grid>
    </Container>
  );
}
